.reservationContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 400px;
    margin-top: 20px;
}

h1 {
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    padding-bottom: 10px;
    align-content: left;
    font-size: 3rem;
}

.form-control {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 350px;
    margin: 5px;
}

label {
    line-height: 30px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    width: 50%;
}

  .react-datepicker-wrapper,
.react-datepicker__input-container,
.react-datepicker__input-container input {
    display: block;
    width: 100%;
    height: 30px;
    border-radius: 3px;
    border: 1px solid black;
    cursor: pointer;
}

select, input {
    border-radius: 2px;
    border: 1px solid black;
    cursor: pointer;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
}

.submit {
    background-color: red;
    height: 40px;
    width: 90px;
    color: white;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
}